.advantages {
	min-height: 473px;
	background: #F6F6F6;
	padding: 70px 0 65px;

	&__heading {
		&_level {
			&_2 {
				font-weight: bold;
				font-size: 30px;
				line-height: 30px;
				color: #0D0D0D;
				text-align: center;
				text-transform: uppercase;
			}

			&_3 {
				font-size: 18px;
				line-height: 29px;
				text-align: center;
				margin-top: 40px;
			}
		}
	}

	&__wrapper {
		display: flex;
		justify-content: space-between;
		margin-top: 60px;
	}

	&__icon {
		display: block;
		margin: 0 auto;
	}

	&__descr{
		width: 240px;
		margin-top: 20px;
		font-weight: 300;
		font-size: 14px;
		line-height: 1.3;
		color: #0D0D0D;
		text-align: center;
	}
	&__link{
		font-family: MuseoModerno;
		font-weight: 300;
		color: #FF0000;
		text-transform: uppercase;
		text-decoration: underline;
	}
}
